body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans Condensed', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(78, 78, 83);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  /* box-sizing: border-box; */
}
.row {
  width: 100vw;
  height: 49vh;
  display: flex;
  box-sizing: border-box;
  padding-left: 1%;
}
.row:first-child {
  margin: 5px 0;
}

.row section {
  margin: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;;
}

/*  */
section > svg {
  width: 90% !important;
  height: 90% !important;
  margin: auto;
  color: rgba(105, 98, 98, 0.514);
}

/* section-wise */
.logo,
.education,
.projects,
.work-experience,
.technology-stack,
.photography {
  position: relative;
}

.logo .container,
.education .container,
.projects .container,
.work-experience .container,
.technology-stack .container,
.photography .container{
  overflow-y: auto!important;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

/* focused */
.logo.focused,
.education.focused,
.projects.focused,
.work-experience.focused,
.technology-stack.focused,
.photography.focused {
  z-index: 5;
  opacity: 1;
  border-radius: 5px;
  box-shadow: 0px 0px 30px 1px gray;
}

/* shaded */
.logo.shaded,
.education.shaded,
.projects.shaded,
.work-experience.shaded,
.technology-stack.shaded,
.photography.shaded {
  opacity: 0.2;
  /* background-color: rgb(117, 117, 117); */
}

.logo .placeholder,
.education .placeholder,
.projects .placeholder,
.work-experience .placeholder,
.technology-stack .placeholder,
.photography .placeholder {
  position: absolute !important;
}

.logo {
  width: 30%;
  height: 100%;
  background-color: rgb(172, 136, 54);
  transition: all 0.3s ease-in-out;
  opacity: 1;
  position: relative;
}
.logo.focused {
  transform: scale(1.5, 1.5);
  transform-origin: top left;
  opacity: 1;
}
.logo.shaded {
}

.logo .placeholder {
  transition: all 0.3s ease-in-out;
}
.logo.focused .placeholder {
  position: relative;
  width: 30px!important;
  height: 30px!important;
  top: 1%;
  left: 1%;
}

.logo .container{
  transition: all 0.3s ease-in-out;  
  transform: scale(0,0);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.logo.logo.focused .container{
  transform: scale(1,1);
}

.logo .container .contact {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /* padding: 10px 5px; */
  text-align: center;
  position: absolute;
  align-items: center;
  justify-content: space-around;
  transform: scale(0, 0);
}

.logo.focused .container {
  transform: scale(1, 1);
}

.education {
  width: 40%;
  height: 100%;
  background-color: rgb(146, 122, 92);
  transition: all 0.3s ease-in-out;
  transform: scale(1, 1);
  opacity: 1;
  position: relative;
  /* padding: 20px; */
}

.education.focused {
  z-index: 5;
  transform: scale(1.7, 1.5);
  transform-origin: top;
  opacity: 1;
}
.education.shaded {
  opacity: 0.2;
  /* background-color: rgb(117, 117, 117); */
}

.education svg {
  transition: all 0.3s ease-in-out;
  /* position: absolute; */
}
.education.focused .placeholder {
  position: relative;
  width: 30px!important;
  height: 30px!important;
  top: 1%;
  left: 1%;
  transform: rotate(-20deg);
}

.education .container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 10px 5px;
  text-align: center;
  transform: scale(0,0);
  transition: all 0.3s ease-in-out;  
}

.education.focused .container{
  transform: scale(1,1);
  padding: 2% 12%;
}

.education .container .header {
  text-align: left;
}

.education .container .info {
  padding-top: 10%;
  text-align: left;
}

.education .container .college {
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

}

